































































import { Component, Vue } from 'vue-property-decorator';
@Component
export default class Footer extends Vue {

  private GoRouter(val) {
    if (val == 'medium') {
      window.open('https://medium.com/@NetCloth');
    }
    if (val == 'twier') {
      window.open('https://twitter.com/NetCloth');
    }
    if (val == 'github') {
      window.open('https://github.com/NetCloth');
    }
    if (val == 'wordpress') {
      window.open('https://blog.netcloth.org');
    }
    if (val == 'app') {
      window.open('http://chat-app.netcloth.org');
    }
    if( val =="paper"){
      if(localStorage.getItem("language") == "zh"){
        window.open('https://cdn.jsdelivr.net/gh/netcloth/official-website-main@v0.2.1/dist/docs/whitepaper_cn.pdf');
      }else{
        window.open('https://github.com/netcloth/whitepaper/blob/master/whitepaper.md');
      }
    }
  }

  private GoBlog() {
    if (this.$i18n.locale == 'zh') {
      window.open('https://blog.netcloth.org');
    } else {
      window.open('https://medium.com/@NetCloth');
    }
  }
}
