






















































import { Component, Prop, Vue } from 'vue-property-decorator';
import { UserModule } from '../store/modules/user';

@Component
export default class Header extends Vue {
  private isOpen = false;
  @Prop({
    default: function () {
      return {
        imgSrc: 'https://cdn.jsdelivr.net/gh/netcloth/official-website-main@v0.2.1/dist/images-wap/header/nav-logo.png',
        menuSrc: 'https://cdn.jsdelivr.net/gh/netcloth/official-website-main@v0.2.1/dist/images-wap/header/menu.png',
        menuCloseSrc: 'https://cdn.jsdelivr.net/gh/netcloth/official-website-main@v0.2.1/dist/images-wap/header/menu-close.png',
      }
    }
  }) readonly logoUrl!: Object;
  @Prop({ default: '#ffffff' }) readonly Pcolor!: string;

  get language() {
    return UserModule.language;
  }

  // methods
  private ChangLang(value: string): void {
    UserModule.ResetLanguage(value);
    localStorage.setItem('language', value);
    this.$i18n.locale = value;
    this.language = value;
    window.location.reload();
  }

  private Open() {
    this.isOpen = !this.isOpen;
  }

  private GoBlog() {
    if (this.language == 'zh') {
      window.open('https://blog.netcloth.org');
    } else {
      window.open('https://medium.com/@NetCloth');
    }
  }
  private GoBlock() {
    window.open('https://explorer.netcloth.org');
  }
}
