































import { Component, Vue } from 'vue-property-decorator';
import { Swiper, Slide } from 'vue-swiper-component';
@Component({
  components: {
    Swiper,
    Slide
  }
})
export default class Carousel extends Vue {

  ClickLeft() {
    this.$refs.swiper.prevSlide(); // 
  }
  ClickRight() {
    this.$refs.swiper.nextSlide(); // 
  }
}
