



























































import { Component, Vue } from 'vue-property-decorator';

@Component
export default class Carousel extends Vue {
  private index = 1;
  private Timer = null;
  private loadWidth = 0;
  private loadTimer = null;
  private Width = {
    "width": "0%",
  };


  private mounted() {
    this.startTimer()
  }

  private ClickLeft() {
    this.stopTimer();
    this.index--;
    if (this.index <= 0) {
      this.index = 3;
    }
  }

  private ClickRight() {
    this.stopTimer();
    this.index++;
    if (this.index >= 4) {
      this.index = 1;
    }
  }

  startTimer() {
    this.loadTimer = setInterval(() => {
      this.loadWidth++;
      this.Width = {
        "width": this.loadWidth + '%',
      }
      if (this.loadWidth > 100) {
        this.loadWidth = 0;
        this.index++;
        if (this.index >= 4) {
          this.index = 1;
        }
      }
    }, 100)
  }

  stopTimer() {
    this.loadWidth = 0;
  }
}
